.btn-container {
  width: 80%;
  text-align: center;
  margin: auto; }

.btn {
  padding: .8rem 1.8rem;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  margin: 1rem 0; }
  .btn.btn__blue {
    background-color: #1c69a8;
    color: #ffffff; }

*, *::before, *::after,
*:before, *:after {
  box-sizing: border-box; }

body {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 300; }

h1 {
  font-weight: 400;
  text-align: center; }

.section {
  margin-top: 6rem;
  width: 100%; }

.container {
  width: 100%;
  max-width: 1040px;
  margin: auto; }

fieldset {
  border: none;
  padding: 0; }

.fieldset {
  margin-bottom: 2.5rem; }

input[type="text"] {
  display: block;
  width: 100%;
  height: 2.5rem;
  padding: .7rem;
  border: 1px solid #d3d2d2;
  border-radius: 5px; }
  input[type="text"].wrong-input {
    border: 1px solid #df5e5e; }

input[type="checkbox"] {
  margin-right: 1rem; }

.date input[type="text"] {
  margin-bottom: .5rem; }

.section__budget {
  display: block; }
  .section__budget h2 {
    text-align: center; }
  .section__budget .budget__container {
    padding: 2rem;
    border-radius: 5px;
    background-color: #e9e9e9;
    border: 1px solid #d3d2d2; }
  .section__budget .output {
    font-weight: 400;
    font-size: 1.8rem; }
  .section__budget .total__conatiner {
    padding: 2rem;
    background-color: #ffffff;
    border: 1px solid #d3d2d2; }

.hide {
  display: none; }

@media screen and (min-width: 768px) {
  .small-fields {
    min-width: 25%; }
  .flex__container {
    display: flex; }
  .flex-item {
    margin-right: 2rem; } }
